<script>
import CoreService from '@services/core'
import { toPrice } from '@plugins/money'

export default {
    props: {
        items: {
            type: Array,
            default: () => [],
        },
        invoice: {
            type: Object,
            required: true,
        },
        patientVisit: {
            type: Object,
            required: true,
        },
    },

    data () {
        return {
            fields: [
                {
                    name: 'date',
                    title: 'Date',
                    callback: this.$options.filters.date_DATE_MONTH_YEAR,
                },
                {
                    name: 'description',
                    title: 'Description',
                },
                {
                    name: 'total',
                    title: 'TOTAL',
                    callback: function (value) {
                        if (value < 0) {
                            return '(' + toPrice(Math.abs(value)).toFormat('0,0.00') + ')'
                        }
                        return toPrice(value).toFormat('0,0.00')
                    },
                    titleClass: 'text-end pe-3',
                    dataClass: 'text-end pe-3',
                    width: '10%',
                },
                {
                    name: 'billed_by',
                    title: 'Requested by',
                },

                {
                    name: '__slot:actions',
                    dataClass: 'text-right',
                },
            ],

            bills: [],

            form: new window.Form({
                id: null,
                item_id: '',
                comments: '',
                ledger_id: '',
                date: new Date().toISOString().slice(0, 10),
                quantity: 1,
                unit_price: 1,
                items: [],
            }),

            invoices: [],

            productPrice: 0,
        }
    },
    computed: {
        vuetable () {
            return this.$refs.table.$refs.vuetable
        },
        radiologyItems () {
            this.retrievePatientBills()
            return this.items.filter((item) => item.department.name === 'Radiology')
        },

    },

    watch: {
        'form.item_id': function (newId) {
            if (newId) {
                this.findProductPriceSetup(newId)
            }
        },
    },

    methods: {
        url () {
            return '/patient-billing/patient-bills/' + this.invoice.hashid
        },
        retrievePatientBills () {
            this.$httpClient.get('/patient-billing/patient-bills/' + this.invoice.hashid)
                .then(({ data }) => {
                    this.bills = data.bills.filter(item => item.department_name === 'Radiology')
                    console.log(data)
                }).catch(() => {
                this.$toast.error('There was a problem retrieving the patient bills')
            })
        },
        findProductPriceSetup (itemId) {
            const item = this.items.find((item) => item.id === itemId)

            if (this.invoice.cash_credit === 0) {
                this.form.unit_price = item.cash_price ? item.cash_price.charge_value * 0.01 : 0
            }

            if (this.invoice.cash_credit === 1) {
                this.form.unit_price = item.scheme_price ? item.scheme_price.charge_value * 0.01 : 0
            }
        },
        submitRadiologyTestRequest () {
            this.form.processing = true

            this.form.ledger_id = this.invoice.id

            const vm = this

            const test = this.items.find((item) => item.id === vm.form.item_id)

            if (this.invoice.cash_credit === 0) {
                if (!test.cash_price) {
                    this.$toast.error('The test billing price has not been set')
                    return
                }
            }

            if (this.invoice.cash_credit === 1) {
                if (!test.scheme_price) {
                    this.$toast.error('The test billing price has not been set')
                    return
                }
            }

            const item = {
                description: test.name,
                item_id: this.form.item_id,
                quantity: 1,
                unit_price: this.form.unit_price,
                ledger_id: this.form.ledger_id,
            }

            this.form.items.push(item)

            this.form.post('/patient-bills')
                .then(() => {
                    this.vuetable.reload()
                    this.retrievePatientBills()
                    this.$toast.success('Test has been requested')
                    this.cleanUp()
                    this.$bvModal.hide('radiology-modal')
                })
                .catch()
        },

        deleteTest (rowData) {
            console.log('d')
            if (rowData.paid) {
                this.$toast.error('The test has been paid for. You cannot paid transactions')
                return
            }

            this.$toast.question('Do you want to continue?', 'Deleting Test!')
                .then(() => {
                    this.$httpClient.post('/bills/reverse', {
                        comments: 'Deleted by doctor',
                        detail_id: rowData.id,
                        ledger_id: this.invoice.id,
                        source: '1',
                    }).then(() => {
                        this.retrievePatientBills()
                        this.vuetable.reload()
                        this.$toast.success('The test has been deleted')
                    }).catch(() => {
                        this.$toast.error('The test could not be deleted. Please try again later')
                    })
                })
        },

        cleanUp () {
            this.vuetable.reload()
            this.form.reset()
        },

        findProcedureForVisit () {
            this.vuetable.reload()
        },

        findAllInvoices () {
            CoreService.findAllHeaders({
                sort: '-date',
                page: { size: 20000 },
                filter: { patient_id: this.invoice.patient_id },
            })
                .then(({ data }) => {
                    this.invoices = data
                }).catch()
        },
    },
}
</script>

<template>
    <div>
        <div class="card rounded-3">
            <div class="card-header">
                <h2>Radiology</h2>

                <div class="card-actions">
                    <button
                        v-if="invoice.processed_at === null"
                        v-b-modal:radiology-modal
                        class="btn btn-outline-primary"
                    >
                        <i class="uil uil-plus me-1"/>
                        Request Procedure
                    </button>
                </div>
            </div>
            <vue-table
                ref="table"
                :data="bills"
                :api-mode="false"
                :fields="fields"
            >
                <template #actions="props">
                    <div v-if="invoice.processed_at === null" class="dropdown">
                        <button
                            class="btn dropdown-toggle align-text-top"
                            data-bs-toggle="dropdown"
                        >
                            Menu
                        </button>
                        <div class="dropdown-menu dropdown-menu-end">
                            <a class="dropdown-item text-danger" href="#" @click="deleteTest(props.rowData)">
                                <i class="uil uil-trash-alt me-2"/> Delete
                            </a>
                        </div>
                    </div>
                </template>
            </vue-table>
        </div>

        <b-modal
            id="radiology-modal"
            title="Radiology Form"
            no-close-on-backdrop
            hide-header-close
            content-class="rounded-3 bg-white"
            body-class="px-4"
            modal-class="modal-blur"
            footer-class="d-flex justify-content-between border-top bg-light"
            @hidden="cleanUp"
        >
            <form id="form" @submit.prevent="submitRadiologyTestRequest">
                <div class="form-group mb-4">
                    <label class="form-label">Procedure</label>
                    <!--          {{ radiologyItems }}-->
                    <v-select
                        v-model="form.item_id"
                        :options="radiologyItems"
                        label="name"
                        :clearable="false"
                        :reduce="(option) => option.id"
                    >
                        <template #selected-option="item">
                            <div>
                                <div class="font-weight-medium">
                                    {{ item.name }}
                                </div>
                                <div v-if="invoice.cash_credit === 0" class="text-primary small">
                                    KES {{
                                        (item.cash_price ? item.cash_price.charge_value * 0.01 : 0) | toMoneyFormat
                                    }}
                                </div>
                                <div v-else class="text-primary small">
                                    KES {{
                                        (item.scheme_price ? item.scheme_price.charge_value * 0.01 : 0) | toMoneyFormat
                                    }}
                                </div>
                            </div>
                        </template>
                    </v-select>
                    <span
                        v-if="form.hasError('item_id')"
                        class="text-danger"
                        v-text="form.getError('item_id')"
                    />
                </div>

                <div class="mb-3">
                    <label for="" class="form-label">Comments <small>(optional)</small></label>
                    <textarea
                        id="note"
                        v-model="form.comments"
                        type="text"
                        class="form-control"
                        name="comments"
                    />

                    <span
                        v-if="form.errors.has('comments')"
                        class="text-danger"
                        v-text="form.errors.get('comments')"
                    />
                </div>
            </form>

            <template #modal-footer="{cancel}">
                <button
                    class="btn btn-light me-2"
                    :disabled="form.processing"
                    @click="cancel"
                >
                    Close
                </button>

                <button
                    form="form"
                    type="submit"
                    class="btn btn-primary"
                    :disabled="form.processing"
                >
                    Request Procedure
                </button>
            </template>
        </b-modal>
    </div>
</template>
